<template>
  <form label-width="100px">
    <ul>
      <li>
        <label>邮箱</label>
        <input v-model="user.name"/>
      </li>
      <li>
        <label prop="pass">密码</label>
        <input type="password" v-model="user.password" auto-complete="off"/>
      </li>
      <li>
        <label prop="checkPass">确认密码</label>
        <input type="password" v-model="user.checkPass" auto-complete="off"/>
      </li>
      <li>
        <button type="primary" @click="submitForm">注册</button>
        <button @click="resetForm">重置</button>
      </li>
    </ul>
  </form>
</template>

<script>
const axios = require('axios')
export default {
  name: 'register',
  data () {
    return {
      user: {
        name: '',
        password: '',
        checkPass: ''
      }
    }
  },

  methods: {
    send: function () {
      axios({
        method: 'post',
        url: '/register',
        data: {'mail': this.user.name, 'password': this.user.password},
        header: {
          // 如果写成contentType会报错
          'Content-Type': 'application/json'
        }
      }).then(function (resp) {
        console.log(resp.data)
        console.log(resp.data['uid'])
        if (resp.data['uid'] !== '') {
          alert('注册成功！')
        }
      }).catch(resp => {
        console.log('请求失败：' + resp.status + ',' + resp.statusText)
      })
    },

    checkMail: function () {
      var re = /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/
      if (re.test(this.user.name)) {
        return true
      } else {
        return false
      }
    },

    submitForm () {
      if (this.user.name === '') {
        alert('请输入用户名')
      } else if (this.user.password === '') {
        alert('请输入密码')
      } else if (this.user.checkPass === '') {
        alert('请确认密码')
      } else if (this.user.password !== this.user.checkPass) {
        alert('密码不一样！')
      } else if (this.user.password.length < 6 || this.user.password.length > 18) {
        alert('密码长度必须在6-18之间！')
      } else if (this.checkMail() !== true) {
        alert('邮箱不合法！请重新输入！')
      } else {
        this.send()
      }
    },

    resetForm () {
      this.user.name = ''
      this.user.password = ''
      this.user.checkPass = ''
    }
  }
}
</script>

<style scoped>
  li {
    list-style: none;
  }
  label {
    display: inline-block;
    width: 80px;
    height:30px;
    line-height:30px;
    vertical-align: middle;
    text-align: justify;
    text-align-last: justify
  }
</style>



